import PokemonList from "../../components/pokemonList";
import PokemonSearch from "../../components/pokemonSearch";
import { useEffect, useState } from "react";
import Pokemon from "../../models/pokemon";
import { useTranslation } from "react-i18next";
import { useParams } from "react-router-dom";
import PokemonService from "../../services/PokemonService";

const Pokedex = () => {
  const { t } = useTranslation();

  const params = useParams();

  const [pokemonOrigin, setPokemonOrigin] = useState<Pokemon[]>([]);
  const [pokemonL, setPokemonL] = useState<Pokemon[]>([]);

  const [searchText, setSearchText] = useState<string | undefined>(
    params.filter
  );
  const [sortBy, setSortBy] = useState<string>("id");
  const [sortDirection, setSortDirection] = useState<number>(1);

  /* met  jour la liste des pokemons avec un pokemon modifi */
  const handlePokemonChange = (pok: Pokemon) => {
    let result: Pokemon[];

    // mthode a : supprime le pokemon  modifier puis le rajoute  la fn de la liste
    // tape 1 : suppression du pokemon de la liste
    result = pokemonOrigin.filter((p: Pokemon) => p.id !== pok.id);
    // tape 2 : ajout de la version modifie du pokemon  la fin de la liste
    result.push(pok);

    /*
    // mthode b : on recherche la position du pokemon  modifier puis on le remplace par la nouvelle version
    // tape 1 : initialisation de result avec tous les pokemons
    result = pokemonOrigin;
    // tape 2 : recherche de l'emplacement du pokemon  modifier
    const idx: number = result.findIndex((p: Pokemon) => p.id === pok.id);
    //tape 3 : remplacement du pokemon  la position trouve par sa nouvelle version
    result.splice(idx, 1, pok);
    */

    setPokemonOrigin(result);
  };

  const handlePokemonDelete = (id: number) => {
    setPokemonOrigin(pokemonOrigin.filter((pokemon) => pokemon.id !== id));
  };

  // effet qui se dclenche si un des lment parmis [sortBy, searchText, sortDirection, t] est modifi
  useEffect(() => {
    // on repart de la liste complte des pokmons et on la fitre en fonction de searchText

    if (searchText !== undefined && searchText !== "") {
      PokemonService.getStartWith(searchText.toLowerCase()).then((value) =>
        setPokemonOrigin(value)
      );
    } else {
      PokemonService.getAll().then((value) => setPokemonOrigin(value));
    }
  }, [searchText]);

  useEffect(() => {
    // la liste filtre est ensuite trie
    let tempSorted: Pokemon[] = pokemonOrigin.sort((a: Pokemon, b: Pokemon) => {
      let valA: number | string = a.id;
      let valB: number | string = b.id;

      if (sortBy === "name") {
        valA = t("pokemon." + a.id);
        valB = t("pokemon." + b.id);
      } else if (sortBy === "CP") {
        valA = a.cp;
        valB = b.cp;
      }

      return valA > valB ? sortDirection : -sortDirection;
    });

    // le rsultat obtenu est mis dans pokemonL qui est la liste des pokmons affichs
    setPokemonL(tempSorted);
  }, [sortBy, sortDirection, pokemonOrigin, t]);

  return (
    <>
      <PokemonSearch
        searchText={searchText}
        searchUpdate={setSearchText}
        sortBy={sortBy}
        sortByUpdate={setSortBy}
        sortDirection={sortDirection}
        sortDirectionUpdate={setSortDirection}
      />
      <PokemonList
        pokemons={pokemonL}
        handlePokemonChange={handlePokemonChange}
        handlePokemonDelete={handlePokemonDelete}
      />
    </>
  );
};

export default Pokedex;
